import React, { Component } from 'react'
import { HashRouter, Redirect, Route, Switch } from 'react-router-dom'
import './App.css'
import MicroApp from '@workgrid/micro-app'
import Summary from './scenes/Summary'
import Detail from './scenes/Detail'

class App extends Component {
  constructor() {
    super()
    // Create a new instance of a MicroApp
    this.microapp = new MicroApp({ audience: window.location.origin })
    this.iFrameSrc = ''
  }

  render() {
    return (
      <HashRouter>
        <div>
          <Switch>
            <Route exact path="/" render={() => <Redirect to="/summary" />} />
            <Route exact path="/summary" render={props => <Summary {...props} app={this.microapp} />} />
            <Route exact path="/detail" render={props => <Detail {...props} app={this.microapp} />} />
            <Route
              render={() => (
                <div>
                  <h2>No Route found</h2>
                </div>
              )}
            />
          </Switch>
        </div>
      </HashRouter>
    )
  }
}

export default App
