import React, { Component } from 'react'

class Summary extends Component {
  constructor() {
    super()
    this.state = {
      feedback: ''
    }

    this.showDetail = this.showDetail.bind(this)
  }

  componentDidMount() {
    this.props.app.initialize()
  }

  showDetail() {
    this.props.app.showDetail({
      url: `${window.location.href.split('#')[0]}#/detail`,
      title: 'Starter Detail'
    })

    this.setState({
      feedback: 'Show Detail Sent'
    })
  }

  render() {
    const { feedback } = this.state
    return (
      <div className="App">
        <p>Test a non-ready detail (APP-3)</p>
        <pre>{feedback}</pre>
        <div className="ActionBar">
          <button onClick={this.showDetail}>Show Detail</button>
        </div>
      </div>
    )
  }
}

export default Summary
